<template>
  <div id="style-operate">
    <!-- Form -->
    <el-dialog v-model="dialogFormVisible" :before-close="beforeClose" :title="titleName" width="1000" draggable>
      <el-form :inline="true" :model="form" :rules="rules" ref="ruleForm" class="demo-form-inline" :label-width="100" label-position="right">

        <el-form-item :label="$t('MenuView.parent')" :label-width="formLabelWidth">
          <el-cascader ref="tree" :options="selectData.resourceList" v-model="form.pids" @change="getCheckedNodesLength" :placeholder="$t('MenuView.parentPlaceholder')" :disabled="type != 'add'" style="width: 300px" :props="props1" clearable/>
        </el-form-item>

        <el-form-item :label="$t('MenuView.name')" prop="name" :label-width="formLabelWidth">
          <el-input :readonly="isReadonly" v-model="form.name" :placeholder="$t('MenuView.namePlaceholder')" autocomplete="off"/>
        </el-form-item>

        <el-form-item :label="$t('MenuView.nameEn')" prop="nameEn" :label-width="formLabelWidth">
          <el-input :readonly="isReadonly" v-model="form.nameEn" :placeholder="$t('MenuView.nameEnPlaceholder')" autocomplete="off"/>
        </el-form-item>

        <el-form-item :label="$t('MenuView.sort')" :label-width="formLabelWidth">
          <el-input-number v-model="form.sort" :placeholder="$t('MenuView.sortPlaceholder')" :readonly="isReadonly" class="mx-4" :step="1" :min="1" controls-position="right" style="width: 100%"/>
        </el-form-item>

        <el-form-item :label="$t('MenuView.symbol')" prop="symbol" v-if="threeLength != 1" :label-width="formLabelWidth">
          <el-input :readonly="isReadonly" v-model="form.symbol" :placeholder="$t('MenuView.symbolPlaceholder')" autocomplete="off"/>
        </el-form-item>

<!--        <el-form-item label="图标" :label-width="formLabelWidth">-->
<!--          <el-input :readonly="isReadonly" v-model="form.icon" placeholder="请输入图标" autocomplete="off"/>-->
<!--        </el-form-item>-->

        <el-form-item :label="$t('MenuView.icon')" v-if="threeLength != 3" :label-width="formLabelWidth">
          <el-input v-model="form.icon" :placeholder="$t('MenuView.iconPlaceholder')" autocomplete="off" color="#626aef" >
            <template #append >
              <el-button icon="Search" @click="handleIconClick" class="custom-button"></el-button>
            </template>
          </el-input>
        </el-form-item>

        <el-form-item :label="$t('MenuView.href')" prop="href" v-if="threeLength != 3" :label-width="formLabelWidth">
          <el-input :readonly="isReadonly" v-model="form.href" :placeholder="$t('MenuView.hrefPlaceholder')" autocomplete="off"/>
        </el-form-item>

        <el-form-item :label="$t('MenuView.remark')" :label-width="formLabelWidth">
          <el-input :readonly="isReadonly" v-model="form.remark" :placeholder="$t('MenuView.remarkPlaceholder')" autocomplete="off"/>
        </el-form-item>
      </el-form>

      <template #footer>
      <span class="dialog-footer">
        <el-button @click="closeButton()">
          {{ $t('common.button.cancel') }}
        </el-button>
        <el-button type="primary" v-show="type != 'detail'" @click="submitButton()">
          {{ $t('common.button.confirm') }}
        </el-button>
      </span>
      </template>
    </el-dialog>

    <!-- 图标选择组件 -->
    <iconView ref="icon" @childClick="setIcon"/>
  </div>
</template>

<script>
import {getResourceInfoPullDown} from "@/http/PullDownApi";
import {ElNotification} from "element-plus";
import {saveResourceInfo, updateResourceInfo} from "@/http/basics/ResourceInfo";
import iconView from "@/views/common/iconView";


export default {
  components: {iconView},
  name: "style-operate",
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      // 控制对话框大开闭
      dialogFormVisible: false,
      // 联动列表数据
      rowData: {},
      // 行数据副本
      form: {
        sort: 1,
        pid: 0,
        pids: [0],
      },
      // 表单验证规则
      rules: {
        name: [
          { required: true, message: this.$t('MenuView.namePlaceholder'), trigger: 'blur' },
        ],
        nameEn: [
          { required: true, message: this.$t('MenuView.nameEnPlaceholder'), trigger: 'blur' },
        ],
        symbol: [
          { required: true, message: this.$t('MenuView.symbolPlaceholder'), trigger: 'blur' },
        ],
        href: [
          { required: true, message: this.$t('MenuView.hrefPlaceholder'), trigger: 'blur' },
        ],
      },
      // 进入类型: 新增 详情 编辑
      type: '',
      titleName: this.$t('common.dialog.defaultTitle'),
      // 表单的只读
      isReadonly: false,
      formLabelWidth: '120px',

      selectData: {}, // 下拉的数据集合对象
      props1: {
        checkStrictly: true,
        value: 'id', // 用于绑定到 v-model 的字段名，默认是 'value'
        label: 'name', // 显示在下拉菜单中的字段名，默认是 'label'
        children: 'resourceInfoList', // 子节点数组的字段名，默认是 'children'
      },
      threeLength: 1, // 联级选择的长度
    }
  },

  // watch 侦听器 能够侦听到data数据的变化 且获取对应的 新值 和 旧值
  watch: {
    type(newVal, oldVal) {
      if (newVal == '') return
      if (newVal == 'add') this.titleName = this.$t('common.dialog.addTitle')
      if (newVal == 'edit') this.titleName = this.$t('common.dialog.editTitle')
      if (newVal == 'detail') this.titleName = this.$t('common.dialog.detailTitle')
    },
  },

  // moubted: 页面加载完毕后执行
  mounted() {

  },

  // methods: 对象中的方法，可以在页面中调用
  methods: {
    getPullDownData() {
      // 获取权限树的数据
      getResourceInfoPullDown({type: 2}).then(res => {
        this.selectData.resourceList = res.data.data;
      }).catch((erro) => { });
    },
    // 判断联级选择的长度 1_顶层父级 2_一级菜单 3_页面按钮
    getCheckedNodesLength() {
      let resource = this.$refs.tree.getCheckedNodes();
      if (resource.length == 0) {
        this.threeLength = 1;
      } else {
        this.threeLength = resource[0].pathValues.length + 1;
      }
      this.$refs.ruleForm.resetFields(); // 重置表单验证
    },
    // 这里是图标点击事件的处理逻辑
    handleIconClick() {
      this.$refs.icon.drawer = true;
      if (this.form.icon != null) {
        this.$refs.icon.iconStr = this.form.icon;
      }
    },
    // 图标选择
    setIcon(icon) {
      this.form.icon = icon;
    },

    // 点击遮罩和x时执行
    beforeClose(done) {
      this.closeButton();
      done(); // 必须调用 done，以继续关闭对话框
    },
    // 取消按钮
    closeButton() {
      this.dialogFormVisible = false;
      this.isReadonly = false;
      this.form = {
        sort: 1,
        pid: 0,
        pids: [0],
      };
      this.threeLength = 0, // 联级选择的长度
      this.rowData = {};
      this.$refs.ruleForm.resetFields(); // 重置表单验证
    },
    // 确认按钮
    submitButton() {
      console.log(this.form, 'form')
      // 提交校验
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          let resource = this.$refs.tree.getCheckedNodes();
          if(resource.length != 0) {
            // 通过 ref 获取 el-cascader 实例的方法获取选中的数据
            this.form.resourceInfo = resource[0].data;
          }
          // 判断排序的值是否为数字
          if (isNaN(this.form.sort)) {
            ElNotification({title: this.$t('common.message.tipsTitle'), message: this.$t('common.message.sortNumber'), type: 'warning',})
            return;
          }
          if (this.type == "add") {
            this.form.pid = this.form.pids[this.form.pids.length - 1];
            saveResourceInfo(this.form).then(res => {
              if (res.data.status == 200) {
                this.dialogFormVisible= false;
                ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
                // 刷新列表
                this.$emit('operateClick');
              }
            }).catch((erro) => { });
          } else if (this.type == "edit") {
            updateResourceInfo(this.form).then(res => {
              if (res.data.status == 200) {
                this.dialogFormVisible= false;
                ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
                // 刷新列表
                this.$emit('operateClick');
              }
            }).catch((erro) => { });
          }
          this.closeButton();
        } else {
          return false;
        }
      });
    },
  },
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
.el-button--text {
  margin-right: 15px;
}

.el-select {
  width: 300px;
}

.el-input {
  width: 300px;
}

.dialog-footer button:first-child {
  margin-right: 10px;
}

.avatar-uploader .avatar {
  width: 178px;
  height: 178px;
  display: block;
}
/* 输入框后面图标的样式 */
.el-input-group__append button.el-button {
  border-color: white;
  background-color: #409eff;
  color: #eaeaef;
}
</style>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed var(--el-border-color);
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: var(--el-transition-duration-fast);
}

.avatar-uploader .el-upload:hover {
  border-color: var(--el-color-primary);
}

.el-icon.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  text-align: center;
}

/* 数字输入框 */
/deep/ .el-input-number .el-input__inner {
  text-align: left;
}
</style>
