<template>
  <div class="moduleBoxView">
    <div class="moduleFrom">
      <el-form :inline="true" :model="fromData" class="demo-form-inline">
<!--        <el-form-item label="活动名称: ">-->
<!--          <el-input v-model="fromData.name" placeholder="请输入活动名称" clearable></el-input>-->
<!--        </el-form-item>-->
<!--        <el-form-item label="测试日期: ">-->
<!--          <el-date-picker v-model="fromData.dateTime" type="date" format="YYYY-MM-DD" value-format="YYYY-MM-DD" placeholder="请选择时间" />-->
<!--        </el-form-item>-->
        <el-form-item>
          <el-button type="primary" @click="listData()">
            {{ $t('common.button.search') }}
          </el-button>
          <el-button type="success" v-show="isButton('resource.add')" @click="add()">
            {{ $t('common.button.add') }}
          </el-button>
        </el-form-item>
      </el-form>
    </div>

    <div class="moduletTable" ref="tableDiv">
      <el-table :data="tableData" :height="tabHeight" :empty-text="$t('common.table.noData')" stripe fit highlight-current-row style="width: 100%;margin-bottom: 20px;" row-key="id" border :default-expand-all="false" :tree-props="{children: 'resourceInfoList', hasChildren: 'hasChildren'}">
        <el-table-column prop="name" :label="$t('MenuView.name')" sortable width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="nameEn" :label="$t('MenuView.nameEn')" sortable width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="symbol" :label="$t('MenuView.symbol')" sortable width="220" show-overflow-tooltip></el-table-column>
        <el-table-column prop="icon" :label="$t('MenuView.icon')" sortable width="100">
          <template v-slot="{ row }"><component :is="row.icon" style="width: 20px"/></template>
        </el-table-column>
        <el-table-column prop="resourceType" :label="$t('MenuView.resourceType')" sortable width="120">
          <template v-slot="{ row }">
            <el-tag v-if="row.resourceType === 1">{{ $t('pullDown.resourceType.parent') }}</el-tag>
            <el-tag v-else-if="row.resourceType === 2" class="ml-2" type="success">{{ $t('pullDown.resourceType.page') }}</el-tag>
            <el-tag v-else-if="row.resourceType === 3" class="ml-2" type="danger">{{ $t('pullDown.resourceType.button') }}</el-tag>
            <el-tag v-else class="ml-2" type="info">{{ $t('pullDown.unknown') }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="href" :label="$t('MenuView.href')" sortable width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="sort" :label="$t('MenuView.sort')" sortable width="80" show-overflow-tooltip></el-table-column>
        <el-table-column prop="remark" :label="$t('MenuView.remark')" sortable  show-overflow-tooltip></el-table-column>
        <el-table-column fixed="right" :label="$t('common.table.operation')" width="250">
          <template #default="scope">
            <el-button type="warning" size="small" @click="goOperate(scope.$index, scope.row, 'edit')" v-show="isButton('resource.update')">
              {{ $t('common.button.edit') }}
            </el-button>
            <el-button type="danger" size="small" @click="deleteRow(scope.$index, scope.row)" v-show="isButton('resource.delete')">
              {{ $t('common.button.delete') }}
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <resource-info-operate ref="operate" @operateClick="listData"/>
  </div>
</template>


<script>
import {resourceIsUser} from "@/util/resource";
import {deleteResourceInfoById, listResourceInfo} from "@/http/basics/ResourceInfo";
import resourceInfoOperate from "@/views/basics/ResourceInfo/ResourceInfo-operate";
import {ElLoading, ElNotification} from "element-plus";

export default {
  components: {resourceInfoOperate},
  data() {
    return {
      tabHeight: 0, // 表格高度
      fromData: {},
      tableData: [],
    }
  },
  // moubted: 页面加载完毕后执行
  mounted() {
    this.refreshTableHeight();
    this.listData();
  },
  methods: {
    // 刷新表格高度
    refreshTableHeight() {
      // 创建一个 ResizeObserver 实例
      const resizeObserver = new ResizeObserver(entries => {
        // entries 是 ResizeObserverEntry 对象的数组，包含观察到的元素信息
        for (const entry of entries) {
          if (entry.target === this.$refs.tableDiv) {
            // 如果观察到的元素是你要监听的元素
            // console.log('窗口大小改变，新的表格高度：', entry.contentRect.height);
            this.tabHeight = this.$refs.tableDiv.offsetHeight - 20;
          }
        }
      });
      // 将 ResizeObserver 注册到要观察的元素
      resizeObserver.observe(this.$refs.tableDiv);
    },
    // 获取按钮权限
    isButton(symbolStr) {
      return resourceIsUser(symbolStr);
    },

    // 列表数据请求
    listData() {
      const loading = ElLoading.service({
        lock: true, text: this.$t('common.loadingText'), background: 'rgba(0, 0, 0, 0.7)',}
      );
      listResourceInfo(this.fromData).then(res => {
        this.tableData = res.data.data;
      }).catch((erro) => {
        console.log(erro, 'erro')
      }).finally(() => {
        // 请求无论成功/失败 都执行关闭loading
        loading.close();
      });
    },

    // 新增
    add() {
      this.$refs.operate.dialogFormVisible = true;
      this.$refs.operate.type = "add";
      this.$refs.operate.threeLength = 1;
      this.$refs.operate.getPullDownData();
    },
    // 详情 / 编辑 执行方法
    goOperate(index, row, type) {
      this.$refs.operate.dialogFormVisible = true;
      this.$refs.operate.type = type;
      this.$refs.operate.getPullDownData();
      this.$refs.operate.threeLength = row.resourceType;
      if (type == "detail") {
        // 开启只读 | 深拷贝创建了目标对象的一个完整副本
        this.$refs.operate.isReadonly = true;
        this.$refs.operate.form = JSON.parse(JSON.stringify(row));
      } else if (type == "edit") {
        this.$refs.operate.rowData = row;
        this.$refs.operate.form = JSON.parse(JSON.stringify(row));
      }
    },
    // 删除
    deleteRow(index, row) {
      this.$confirm(this.$t('common.dialog.deleteTips') , this.$t('common.dialog.deleteTitle'), {
        confirmButtonText: this.$t('common.button.confirm'), cancelButtonText: this.$t('common.button.cancel'), type: 'warning'
      }).then(() => {
        // 后台请求删除数据库数据
        deleteResourceInfoById({ id: row.id}).then(res => {
          if (res.data.status == 200) {
            this.listData();
            ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
          }
        }).catch(error => { });
      })
    }
  },
}
</script>


<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
/*导航小标题*/
.el-breadcrumb {
  padding: 0px 0px 20px 0px;
}


</style>
